.main-block {
  margin-top: 64px;
  @include md {
    margin-top: 20px; }
  @include lg {
    margin-top: 65px; }

  .card-1, .card-2, .card-3, .card-4 {
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-position: top;
    @include md {
      min-height: 167px;
      background-position: center; }
    @include lg {
      min-height: 260px;
      background-position: center; } }

  .card-1 {
    background-image: linear-gradient(rgba(33,22,22, .7), rgba(33,22,22, .7)),url('../img/card-1.jpg');
    grid-area: card-1;
    min-height: 247px;
    @include md {
      min-height: 167px; }
    @include lg {
      min-height: 260px; } }

  .card-2 {
    background-image: linear-gradient(rgba(33,22,22, .7), rgba(33,22,22, .7)),url('../img/card-2.jpg');
    grid-area: card-2;
    min-height: 247px;
    @include md {
      min-height: 167px; }
    @include lg {
      min-height: 260px; } }

  .card-3 {
    background-image: linear-gradient(rgba(33,22,22, .7), rgba(33,22,22, .7)),url('../img/card-3.jpg');
    grid-area: card-3;
    min-height: 247px;
    @include md {
      min-height: 167px; }
    @include lg {
      min-height: 260px; } }

  .card-4 {
    background-image: linear-gradient(rgba(33,22,22, .7), rgba(33,22,22, .7)),url('../img/card-4.jpg');
    min-height: 111px;
    grid-area: card-4;
    @include md {
      min-height: 116px; }
    @include lg {
      min-height: 180px; } }

  .cards {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(1, minmax(1fr, 1fr));
    grid-template-rows: 1fr 1fr;
    grid-template-areas: "card-1" "card-2" "card-3" "card-4";
    @include md {
      grid-template-columns: repeat(auto-fit, minmax(232px, 232px));
      gap: 20px;
      grid-template-areas: "card-1 card-2 card-3" "card-4 card-4 card-4"; }
    @include lg {
      grid-template-columns: repeat(auto-fit, minmax(320px, 360px));
      gap: 2rem;
      grid-template-areas: "card-1 card-2 card-3" "card-4 card-4 card-4"; } }


  .cards-item__title {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    color: #FFFFFF; }

  .cards-item__subtitle {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    text-align: center;
    color: #F16D7F; } }


