header {
  background-color: #222222;

  .header-block {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 18px; }

  .header-block__logo {
    display: flex;
    align-items: center;
    img {
      margin-right: 40px; } }

  .header-block__menu {
    display: flex;
    align-items: center;
    a {
      margin-left: 33px; } }

  .header-block__cabinet {
    display: none;
    @include md {
      display: block; }
    @include lg {
      display: block; } }

  .header-block__cart {
    position: relative;
    display: none;
    @include md {
      display: block; }
    @include lg {
      display: block; } }

  .header-block__cart-count {
    position: absolute;
    top: -10px;
    right: -10px;
    color: #fff;
    width: 19px;
    height: 19px;
    border-radius: 50%;
    background-color: #F16D7F;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px; } }

.header-banner {
  background-color: #F1E4E6;
  .header-banner__wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: none;
    background-position: -220px center;
    background-repeat: no-repeat;
    height: 363px;
    @include md {
      height: 367px;
      background-image: url('../img/slide-1.png');
      background-size: contain;
      background-position: left center;
      justify-content: end; }
    @include lg {
      background-image: url('../img/slide-1.png');
      background-position: -220px center;
      height: 764px;
      background-size: contain; } }

  .header-banner__title {
    position: relative;
    font-weight: 900;
    font-size: 38px;
    line-height: 38px;
    color: #222222;
    width: auto;
    padding-left: 28px;
    padding-top: 10px;
    @include md {
      font-size: 44px;
      width: 45%; }
    @include lg {
      font-size: 48px; }

    &:before {
      position: absolute;
      top: 0;
      left: 0;
      content: '';
      width: 12px;
      height: 92px;
      background-color: #F16D7F; } }

  .header-banner__subtitle {
    font-weight: 700;
    font-size: 20px;
    line-height: normal;
    @include md {
      font-size: 24px; }
    @include lg {
      font-size: 32px; } }
  .red {
    color: #F16D7F; } }




